import React from "react";
import Keypad from "./Keypad";
import Question from "./Question";

class App extends React.Component<AppProps, AppState> {
    constructor(props: AppProps) {
        super(props);
        this.state = {
            difficulty: [0, 0],
            difficultyString: "",
            count: 0,
            done: false,
            wrong: 0,
        };
        this.confirm = this.confirm.bind(this);
        this.done = this.done.bind(this);
        this.wrong = this.wrong.bind(this);
    }

    confirm = (number: number) => {
        this.setState({
            answer: number,
        });
    };

    setDifficulty = (min: number, max: number, string: string) => {
        this.setState({
            difficulty: [min, max],
            difficultyString: string,
        });
    };

    setCount = (number: number) => {
        this.setState({
            count: number,
        });
    };

    wrong() {
        this.setState({
            wrong: this.state.wrong + 1,
        });
    }

    done = (time: number) => {
        this.setState({
            done: true,
            doneTime: time,
        });
    };

    restart = () => {
        this.setState({
            difficulty: [0, 0],
            difficultyString: "",
            count: 0,
            done: false,
            wrong: 0,
        });
    };

    render() {
        const {
            done,
            doneTime,
            difficulty,
            difficultyString,
            count,
            answer,
            wrong,
        } = this.state;

        if (done && doneTime) {
            return (
                <div id="result">
                    <h1>ÎÅùÎÇ¨ÏäµÎãàÎã§!</h1>
                    <div className="result">
                        Î™®Îì† Î¨∏Ï†úÎ•º Ìë∏ÎäîÎç∞ {doneTime}Ï¥àÍ∞Ä Í±∏Î¶¨ÏÖ®ÎÑ§Ïöî.
                    </div>
                    <div className="small">
                        <span>
                            ÎÇúÏù¥ÎèÑ : <span>{difficultyString}</span>
                        </span>
                        <span>
                            Ïò§Îãµ : <span>{wrong} Ìöå</span>
                        </span>
                        <span>
                            Î¨∏Ï†ú : <span>{count} Í∞ú</span>
                        </span>
                        <span>
                            Î¨∏Ï†úÎãπ :{" "}
                            <span>{(doneTime / count).toFixed(2)} Ï¥à</span>
                        </span>
                    </div>
                    <div>
                        <button className="button" onClick={this.restart}>
                            Îã§Ïãú ÏãúÏûë
                        </button>
                    </div>
                </div>
            );
        } else if (difficulty && count) {
            return (
                <>
                    <Question
                        questionDifficulty={difficulty}
                        questionLength={count}
                        wrong={this.wrong}
                        done={this.done}
                        value={answer}
                    />
                    <Keypad confirm={this.confirm} />
                </>
            );
        } else {
            if (!difficulty[0]) {
                return (
                    <ul id="select">
                        <li onClick={() => this.setDifficulty(2, 10, "Ïâ¨ÏõÄ")}>
                            Ïâ¨ÏõÄ (2 ~ 10)
                        </li>
                        <li onClick={() => this.setDifficulty(2, 15, "Î≥¥ÌÜµ")}>
                            Î≥¥ÌÜµ (2 ~ 15)
                        </li>
                        <li onClick={() => this.setDifficulty(2, 20, "Ïñ¥Î†§ÏõÄ")}>
                            Ïñ¥Î†§ÏõÄ (2 ~ 20)
                        </li>
                        <li
                            onClick={() =>
                                this.setDifficulty(11, 20, "ÏïÑÏ£º Ïñ¥Î†§ÏõÄ")
                            }
                        >
                            ÏïÑÏ£º Ïñ¥Î†§ÏõÄ (11 ~ 20)
                        </li>
                    </ul>
                );
            } else {
                return (
                    <ul id="select">
                        <li onClick={() => this.setCount(10)}>10 Î¨∏Ï†ú</li>
                        <li onClick={() => this.setCount(20)}>20 Î¨∏Ï†ú</li>
                        <li onClick={() => this.setCount(50)}>50 Î¨∏Ï†ú</li>
                        <li onClick={() => this.setCount(100)}>100 Î¨∏Ï†ú</li>
                    </ul>
                );
            }
        }
    }
}

export default App;
